<template>
    <v-container class="pa-0" style="position: relative;">
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{settingsText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card style="margin-top: 68px;">
            <v-list two-line active-class="primary--text" style="padding: 0px!important">
                <v-list-item @click="$router.push({name: 'settingsAccount'})">
                    <v-list-item-content>
                        <v-list-item-title>{{accountText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="$router.push({name: 'settingsLanguage'})">
                    <v-list-item-content>
                        <v-list-item-title>{{appLanguageText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="$router.push({name: 'settingsPermissions'})">
                    <v-list-item-content>
                        <v-list-item-title>{{permissionsText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="wantToLogout">
                    <v-list-item-content>
                        <v-list-item-title class="red--text">{{toLogOutText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <!--<v-divider></v-divider>

                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>{{removeAccountText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>-->
            </v-list>
        </v-card>

        <v-dialog
                v-model="dialog"
                max-width="500px"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <h3 style="margin-bottom: 10px">{{aboutToLogOutText}}</h3>
                    <p style="margin-bottom: 7px">{{askingForLogOutConfirmationText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="dialog = false">{{cancelText}}</v-btn>
                    <v-btn color="primary" @click="logoutUser">{{confirmText}}</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>

</template>

<script>
    import {mapActions, mapState} from 'vuex';

    export default {
        name: "SettingsHome",
        data: () => ({
            dialog: false,
            snackbar: false,
            snackbarText: null,
            cancelText: null,
            toLogOutText: null,
            settingsText: null,
            permissionsText: null,
            editText: null,
            closeText: null,
            accountText: null,
            aboutToLogOutText: null,
            confirmText: null,
            askingForLogOutConfirmationText: null,
            removeAccountText: null,
            appLanguageText: null
        }),
        computed: {
            ...mapState('user', ['username', 'description',
                'knownLanguages', 'languagesToLearn', 'avgRating', 'numRatings'])
},
        methods: {
            ...mapActions('user', ['logout']),
            wantToLogout() {
                let self = this;
                self.dialog = true;
            },
            logoutUser() {
                let self = this;

                self.logout().then(() => {
                    self.$store.dispatch('resetAppState').then(() => {
                        self.$router.replace('home');
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            }
},
        created() {
            this.cancelText = this.$i18n.t('messages.TEXT_CANCEL');
            this.toLogOutText = this.$i18n.t('messages.TEXT_TO_LOG_OUT');
            this.settingsText = this.$i18n.t('messages.TEXT_SETTINGS');
            this.permissionsText = this.$i18n.t('messages.TEXT_PERMISSIONS');
            this.editText = this.$i18n.t('messages.TEXT_EDIT');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.accountText = this.$i18n.t('messages.TEXT_ACCOUNT');
            this.aboutToLogOutText = this.$i18n.t('messages.TEXT_YOU_ARE_ABOUT_TO_LOG_OUT');
            this.confirmText = this.$i18n.t('messages.TEXT_CONFIRM');
            this.askingForLogOutConfirmationText = this.$i18n.t('messages.TEXT_ARE_YOU_SURE_YOU_WANT_TO_LOG_OUT');
            this.removeAccountText = this.$i18n.t('messages.TEXT_REMOVE_ACCOUNT');
            this.appLanguageText = this.$i18n.t('messages.TEXT_APP_LANGUAGE');
        }
    }
</script>

<style scoped>

</style>
